import * as React from "react";
import styled from "styled-components";

const ButtonStyle = styled.button`
  color: #3f3d56;
  border-radius: 6px;
  min-width: 150px;
  height: 50px;
  border: none;
  min-width: 155px;
  border: 1px solid #f2d357;
  background: #f2d357;
  transition: all 0.5s;
  font-size: 14px;

  &:hover {
    min-width: 225px;
    border-radius: 10px;
    border: 1px solid #f2d357;
  }
`;

interface Props {
  text: string;
}

function Button(props: Props) {
  return <ButtonStyle>{props.text}</ButtonStyle>;
}

export default Button;
