import * as React from "react";
// @ts-ignore
import mobile from "../../images/illustration-6.svg";
// @ts-ignore
import mobileSecond from "../../images/illustration-5.svg";

import { motion, useViewportScroll, useTransform } from "framer-motion";
import ImageLoader from "../atoms/imageLoader";
import styled from "styled-components";

const InfographicStyle = styled.div`
  .infographic-block {
    background: #fff;
  }
  .infographic-block svg:nth-of-type(1) {
    transform: rotate(180deg) translateY(2px);
  }
  .infographic-block img {
    width: 450px;
    max-width: 270px;
  }

  .infographic-block .columns {
    position: relative;
  }
  .img-block {
    margin-top: 50px;
    min-height: 250px;
  }
  .row:nth-of-type(2) {
    margin-top: 50px;
  }
`;

function Infographic() {
  const { scrollYProgress } = useViewportScroll();
  const scale = useTransform(scrollYProgress, [0, 1], [0.7, 1]);

  return (
    <InfographicStyle>
      <div className="infographic-block container">
        <div className="row">
          <div className="one-half column centered padded">
            <motion.div
              animate={{ y: 30 }}
              transition={{ ease: "easeOut", duration: 1.4 }}
            >
              <h2>Creative designs</h2>
            </motion.div>

            <motion.div
              animate={{ y: 30 }}
              transition={{ ease: "easeOut", duration: 1 }}
            >
              <motion.div style={{ scale }}>
                <motion.div
                  style={{
                    scaleY: scrollYProgress
                  }}
                />
                <p>
                  We make high quality , mobile responsive and SEO compliant
                  websites. If you want to get full exposure for your business,
                  or you just want a personal site to show off your holiday
                  snaps, or wedding photos, we've got you covered!
                </p>
              </motion.div>
            </motion.div>
          </div>
          <div className="one-half column centered padded">
            <motion.div style={{ scale }}>
              <motion.div
                style={{
                  scaleY: scrollYProgress
                }}
              />
              <div className="img-block greyed-on-touch">
                <ImageLoader
                  width="350"
                  imgSrc={mobileSecond}
                  imgAlt={"Mobile"}
                  height="300"
                />
              </div>
            </motion.div>
          </div>
        </div>
        <div className="row">
          <div className="one-half column centered padded">
            <motion.div style={{ scale }}>
              <motion.div
                style={{
                  scaleY: scrollYProgress
                }}
              />
              <div className="img-block greyed-on-touch">
                <ImageLoader
                  width="350"
                  imgSrc={mobile}
                  imgAlt={"Mobile"}
                  height="480"
                />
              </div>
            </motion.div>
          </div>
          <div className="one-half column centered padded">
            <motion.div
              animate={{ y: 30 }}
              transition={{ ease: "easeOut", duration: 1.4 }}
            >
              <h2>Mobile responsive</h2>
            </motion.div>

            <motion.div
              animate={{ y: 30 }}
              transition={{ ease: "easeOut", duration: 1 }}
            >
              <p>
                We make high quality , mobile responsive and SEO compliant
                websites. If you want to get full exposure for your business, or
                you just want a personal site to show off your holiday snaps, or
                wedding photos, we've got you covered!
              </p>
            </motion.div>
          </div>
        </div>
      </div>
    </InfographicStyle>
  );
}

export default Infographic;
